import {Form, Input, Button,  Radio} from 'antd';
import 'moment/locale/zh-cn';
import React from "react";
import { myPost, tellError, tellSuccess} from "../../../../tools";
import {PeopleType} from "../../mainConfig";


const AddUser = () => {
    const onFinish = (values: any) => {
        myPost('/addUser',values).then(r=>{
            let {status,msg} = r.data
            if (status === 'ok'){
                tellSuccess(msg)
            }else {
                throw msg
            }
        }).catch(msg=>{
            tellError(msg)
        })
    };

    const onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };


    return (
        <Form
            name="basic"
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 16 }}
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
        >
            <Form.Item
                label="Id"
                name="userId"
                rules={[{ required: true, message: '!' }]}
            >
                <Input/>
            </Form.Item>
            <Form.Item
                label=""
                name="userName"
                rules={[{ required: true, message: '!' }]}
            >
                <Input/>
            </Form.Item>
            <Form.Item
                rules={[{ required: true, message: '!' }]}
                label="" name="peopleType">
                <Radio.Group>
                    <Radio.Button value={PeopleType.student}>{""}</Radio.Button>
                    <Radio.Button value={PeopleType.teacher}>{""}</Radio.Button>
                    <Radio.Button value={PeopleType.administrators}>{""}</Radio.Button>
                </Radio.Group>
            </Form.Item>
            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                <Button type="primary" htmlType="submit">
                    
                </Button>
            </Form.Item>
        </Form>
    );
};

export default AddUser;
