import React, {useState} from 'react';
import "./index.scss"
import {Button, Form, Input} from "antd";
import {useToLogin} from "../../hooks/myRouter";
import {getUserInformation, myPost, tellError, tellSuccess, tellWaring} from "../../tools";

const ForgetPassword = () => {
    const toLogin = useToLogin()
    const [hasViewQuestion,changeHasViewQuestion]= useState(false)
    const [questions,changeQuestions] = useState({question1:"",question2:""})
    const onFinish = (values: any) => {
        console.log('Success:', values);
        const {userId} = getUserInformation()
        if (!hasViewQuestion){
            myPost('/getQuestion',{
                userId
            }).then(r=>{
                let {msg,status,question1,question2} = r.data
                if (status === 'ok'){
                    tellSuccess(msg)
                    changeHasViewQuestion(true)
                    changeQuestions({question1 ,question2 })
                }else {
                    throw msg
                }
            }).catch((msg)=>{
                tellError(msg)
            })
        }else {
            let {userId,userPassword,userPasswordAgain,Q1,Q2} = values
            if (userPassword === userPasswordAgain){
                myPost('/changePassword',{
                    userId,
                    question1: questions.question1,
                    question2: questions.question2,
                    answer1: Q1.answer,
                    answer2: Q2.answer,
                    newPassword: userPassword
                }).then(r=>{
                    let {msg,status} = r.data
                    if (status === 'ok'){
                        tellSuccess(msg)
                        tellSuccess('')
                        toLogin()
                    }else {
                        throw msg
                    }
                }).catch((msg)=>{
                    tellError(msg)
                })
            }else {
                tellWaring('!')
            }
        }

    };
    const onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };
    if (!hasViewQuestion){
        return    <div className={"ForgetPassword"}>
            <div className="container">
                <div className="form">
                    <h2 className="headline">
                        <div>
                            
                        </div>

                    </h2>
                    <p className="description"></p>

                    <Form
                        name="basic"

                        onFinish={onFinish}
                        onFinishFailed={onFinishFailed}
                        layout="vertical"
                        initialValues={{ modifier: 'public' }}
                    >

                        <Form.Item name="userId" label="Id"
                                   rules={[{ required: true, message: 'Id!' }]}
                        >
                            <Input type="textarea" />
                        </Form.Item>
                        <Form.Item wrapperCol={{   }}>
                            <Button  type="primary" htmlType={"submit"} >
                                
                            </Button>
                        </Form.Item>

                    </Form>

                    <div className="login">
                        <p> <span onClick={toLogin}></span></p>
                    </div>
                </div>
            </div>

        </div>
    }else {
        return    <div className={"ForgetPassword"}>
            <div className="container">
                <div className="form">
                    <h2 className="headline">
                        <div>
                            
                        </div>

                    </h2>
                    <p className="description"></p>

                    <Form
                        name="basic"

                        onFinish={onFinish}
                        onFinishFailed={onFinishFailed}
                        layout="vertical"
                        initialValues={{ modifier: 'public' }}
                    >

                        <Form.Item name="userId" label="Id"
                                   rules={[{ required: true, message: 'Id!' }]}
                        >
                            <Input type="textarea" />
                        </Form.Item>


                        <Form.Item label={`1:  ${questions.question1}`}>

                            <Form.Item
                                name={['Q1', 'answer']}
                                noStyle
                                rules={[{ required: true, message: '1!' }]}
                            >
                                <Input style={{ width: '100%' }} placeholder="1" />
                            </Form.Item>
                        </Form.Item>
                        <Form.Item label={`2:  ${questions.question2}`}>


                            <Form.Item
                                name={['Q2', 'answer']}
                                noStyle
                                rules={[{ required: true, message: '2!' }]}
                            >
                                <Input style={{ width: '100%' }} placeholder="2" />
                            </Form.Item>
                        </Form.Item>

                        <Form.Item name="userPassword" label=""
                                   rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="password" />
                        </Form.Item>
                        <Form.Item name="userPasswordAgain" label=""
                                   rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="password" />
                        </Form.Item>
                        <Form.Item wrapperCol={{   }}>
                            <Button  type="primary" htmlType="submit">
                                
                            </Button>
                        </Form.Item>
                    </Form>

                    <div className="login">
                        <p> <span onClick={toLogin}></span></p>
                    </div>
                </div>
            </div>

        </div>
    }

};

export default ForgetPassword;
