import React from 'react';
import "./index.scss"
import {Button, Form, Input, Radio, Select} from "antd";
import {PeopleType} from "../Main/mainConfig";
import {useToLogin, useToOptions} from "../../hooks/myRouter";
import {myPost, tellError, tellSuccess, tellWaring} from "../../tools";

const { Option } = Select;


const SignIn = () => {
    const toLogin = useToLogin()
    const toStuOptions = useToOptions(PeopleType.student)
    const toTeaOptions = useToOptions(PeopleType.teacher)
    const toAdmOptions = useToOptions(PeopleType.administrators)
    const onFinish = (values: any) => {
        console.log('Success:', values);
        let {userId,userName,userPassword,userPasswordAgain,peopleType,Q1,Q2,userEmail} = values
        if (userPassword === userPasswordAgain){
            myPost('/signIn', {
                userId,
                userName,
                password: userPassword,
                peopleType,

                userEmail,
                question1:Q1.question,
                question2:Q2.question,
                answer1:Q1.answer,
                answer2:Q2.answer
            }).then(r  =>{
                let {msg,status} = r.data
                if (status === "ok"){
                    tellSuccess(msg)
                    let {token} = r.data
                    //
                    sessionStorage['userId'] =userId
                    sessionStorage['userName'] =userName
                    sessionStorage['password'] =userPassword
                    sessionStorage['token'] = token

                    //
                    switch (peopleType as PeopleType) {
                        case PeopleType.student:{
                            toStuOptions()
                            break
                        }
                        case PeopleType.teacher:{
                            toTeaOptions()
                            break
                        }
                        case PeopleType.administrators:{
                            toAdmOptions()
                            break
                        }
                    }


                }else {
                    throw msg
                }
            }).catch((msg)=>{
                tellError(msg)
            })
            return true
        }else {
            tellWaring('!')
            return  false
        }

    };
    const onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <div className={"SignIn"}>
            <div className="container">
                <div className="form">
                    <h2 className="headline">
                        <div>
                            
                        </div>

                    </h2>
                    <p className="description"></p>

                    <Form
                        name="basic"

                        onFinish={onFinish}
                        onFinishFailed={onFinishFailed}
                        layout="vertical"
                        initialValues={{ modifier: 'public' }}
                    >
                        <Form.Item
                            rules={[{ required: true, message: '!' }]}
                            label="" name="peopleType">
                            <Radio.Group>
                                <Radio.Button value={PeopleType.student}>{""}</Radio.Button>
                                <Radio.Button value={PeopleType.teacher}>{""}</Radio.Button>
                                <Radio.Button value={PeopleType.administrators}>{""}</Radio.Button>
                            </Radio.Group>
                        </Form.Item>
                        <Form.Item
                            name="userName"
                            label=""
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item name="userId" label="Id"
                                   rules={[{ required: true, message: 'Id!' }]}
                        >
                            <Input type="textarea" />
                        </Form.Item>
                        <Form.Item name="userEmail" label=""
                                   rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="email" />
                        </Form.Item>

                        {/*?????*/}
                        <Form.Item label="1">
                            <Input.Group compact>
                                <Form.Item
                                    name={['Q1', 'question']}
                                    noStyle
                                    rules={[{ required: true, message: '1!' }]}
                                >
                                    <Select  style={{width:"100%",marginBottom:20}}  placeholder="1">
                                        <Option value="?">?</Option>
                                        <Option value="?">?</Option>
                                        <Option value="?">?</Option>
                                        <Option value="?">?</Option>
                                    </Select>
                                </Form.Item>
                                <Form.Item
                                    name={['Q1', 'answer']}
                                    noStyle
                                    rules={[{ required: true, message: '1!' }]}
                                >
                                    <Input style={{ width: '100%' }} placeholder="1" />
                                </Form.Item>
                            </Input.Group>
                        </Form.Item>
                        <Form.Item label="2">
                            <Input.Group compact>
                                <Form.Item
                                    name={['Q2', 'question']}
                                    noStyle
                                    rules={[{ required: true, message: '2!' }]}
                                >
                                    <Select style={{width:"100%",marginBottom:20}} placeholder="2">
                                        <Option value="?">?</Option>
                                        <Option value="?">?</Option>
                                        <Option value="">?</Option>
                                        <Option value="?">?</Option>
                                    </Select>
                                </Form.Item>
                                <Form.Item
                                    name={['Q2', 'answer']}
                                    noStyle
                                    rules={[{ required: true, message: '2!' }]}
                                >
                                    <Input style={{ width: '100%' }} placeholder="2" />
                                </Form.Item>
                            </Input.Group>
                        </Form.Item>

                        <Form.Item name="userPassword" label=""
                                   rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="password" />
                        </Form.Item>
                        <Form.Item name="userPasswordAgain" label=""
                                   rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="password" />
                        </Form.Item>
                        <Form.Item wrapperCol={{   }}>
                            <Button  type="primary" htmlType="submit">
                                
                            </Button>
                        </Form.Item>
                    </Form>

                    <div className="login">
                        <p> <span onClick={toLogin}></span></p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SignIn;
